/* Home.css */

/* Adjust the position of carousel control arrows */

.poster .carousel.carousel-slider .control-arrow:hover{
    background-color:transparent;
}
.poster{
    background-color:#F0F0F0;

}
.posterImage{
    height: 500px;
}

.posterImage > img {
    display: block;
    width: 100%;
}

.posterImage__overlay {
    position: absolute; 
    padding: 5rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0,0,0,1));
 
}   


.posterImage__title {
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}

.posterImage__runtime {
    font-size: 2rem;
    /* margin-bottom: 1rem; */
}

.posterImage__rating {
    margin-left: 3rem;
}

.posterImage__description {
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width: 50%;
}

.carousel.carousel-slider .control-arrow {
    top: 20rem;
    color: #fff;
    font-size: 26px;
    bottom: 0;
    margin-top: 0;
    padding: 5px;
}

.home2{
    display: none;
}